
.layout {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  height: 100%;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}
.layout-content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
      -ms-flex: 1 1 auto;
          flex: 1 1 auto;
  overflow: hidden;
  position: relative;
}
.layout-view {
  overflow: auto;
  width: 100%;
  -webkit-overflow-scrolling: touch;
  will-change: scroll-position;
}
.layout-header, .layout-footer {
  color: white;
  background: #03a9f4;
  z-index: 1;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

/* layout-padding */
@media only screen and (max-width: 600px) {
.layout-padding {
    padding: 1.5rem 0.5rem;
    /* padding: .75rem 0.25rem 0; */
}
.layout-padding.horizontal {
    padding: 0 0.5rem;
    /* padding: 0 0.25rem; */
}
}
@media only screen and (min-width: 601px) and (max-width: 900px) {
.layout-padding {
    padding: 1.5rem 2rem;
    /* padding: .75rem 1rem; */
    margin: auto;
}
.layout-padding.horizontal {
    padding: 0 2rem;
    /* padding: 0 1rem; */
}
}
@media only screen and (min-width: 901px) and (max-width: 1280px) {
.layout-padding {
    padding: 2.5rem 3rem;
    /* padding: 1.25rem 1.5rem 0; */
    margin: auto;
}
.layout-padding.horizontal {
    padding: 0 3rem;
    /* padding: 0 1.5rem; */
}
}
@media only screen and (min-width: 1281px) {
.layout-padding {
    padding: 3rem 4rem;
    /* padding: 1.5rem 2rem 0; */
    margin: auto;
}
.layout-padding.horizontal {
    padding: 0 4rem;
    /* padding: 0 2rem; */
}
}

